import test from './test.js'

/**
 * @description valueminminvaluemaxmax
 * @param {number} min 
 * @param {number} max 
 * @param {number} value
 */
function range(min = 0, max = 0, value = 0) {
    return Math.max(min, Math.min(max, Number(value)))
}

/**
 * @description px  "xxpx""xxrpx""xxxrpx"uni.upx2px
 * @param {number|string} value px
 * @param {boolean} unit 
 * @returns {number|string}
 */
function getPx(value, unit = false) {
    if (test.number(value)) {
        return unit ? `${value}px` : value
    }
    // rpxpx
    if (/(rpx|upx)$/.test(value)) {
        return unit ? `${uni.upx2px(parseInt(value))}px` : uni.upx2px(parseInt(value))
    }
    return unit ? `${parseInt(value)}px` : parseInt(value)
}

 /**
 * @description  : await uni.$u.sleep(20)20ms
 * @param {number} value  ms 
 * @returns {Promise} promise
 */
function sleep(value = 30) {
    return new Promise((resolve) => {
        setTimeout(() => {
            resolve()
        }, value)
    })
}
