function toast(title, duration = 2000) {
    uni.showToast({
        title: String(title),
        icon: 'none',
        duration
    })
}

/**
 * @description type,
 * @param {String} type ,primary|info|error|warning|success
 * @param {boolean} fill fill
 */
function type2icon(type = 'success', fill = false) {
    // ,success
    if (['primary', 'info', 'error', 'warning', 'success'].indexOf(type) == -1) type = 'success'
    let iconName = ''
    // (2019-12-12),infoprimary
    switch (type) {
    case 'primary':
        iconName = 'info-circle'
        break
    case 'info':
        iconName = 'info-circle'
        break
    case 'error':
        iconName = 'close-circle'
        break
    case 'warning':
        iconName = 'error-circle'
        break
    case 'success':
        iconName = 'checkmark-circle'
        break
    default:
        iconName = 'checkmark-circle'
    }
    // ,-fill,icon,-fill
    if (fill) iconName += '-fill'
    return iconName
}

 /**
 * @description 
 * @param {number|string} number 
 * @param {number} decimals 
 * @param {string} decimalPoint 
 * @param {string} thousandsSeparator 
 * @returns {string} 
 */
function priceFormat(number, decimals = 0, decimalPoint = '.', thousandsSeparator = ',') {
    number = (`${number}`).replace(/[^0-9+-Ee.]/g, '')
    const n = !isFinite(+number) ? 0 : +number
    const prec = !isFinite(+decimals) ? 0 : Math.abs(decimals)
    const sep = (typeof thousandsSeparator === 'undefined') ? ',' : thousandsSeparator
    const dec = (typeof decimalPoint === 'undefined') ? '.' : decimalPoint
    let s = ''
    const toFixedFix = function (n, prec) {
        const k = 10 ** prec
        return `${Math.ceil(n * k) / k}`
    }

    s = (prec ? toFixedFix(n, prec) : `${Math.round(n)}`).split('.')
    const re = /(-?\d+)(\d{3})/
    while (re.test(s[0])) {
        s[0] = s[0].replace(re, `$1${sep}$2`)
    }

    if ((s[1] || '').length < prec) {
        s[1] = s[1] || ''
        s[1] += new Array(prec - s[1].length + 1).join('0')
    }
    return s.join(dec)
}

/**
 * @description duration
 * mssmss
 * 3030030300ms300s
 * @param {String|number} value : "1s"|"100ms"|1|100
 * @param {boolean} unit  : false number
 * @return {string|number} 
 */
function getDuration(value, unit = true) {
    const valueNum = parseInt(value)
    if (unit) {
        if (/s$/.test(value)) return value
        return value > 30 ? `${value}ms` : `${value}s`
    }
    if (/ms$/.test(value)) return valueNum
    if (/s$/.test(value)) return valueNum > 30 ? valueNum : valueNum * 1000
    return valueNum
}

/**
 * @description 
 * @param {String} value 
 */
function padZero(value) {
    return `00${value}`.slice(-2)
}

/**
 * @description u-formu-form
 * @param {*} instance
 * @param {*} event
 */
function formValidate(instance, event) {
    const formItem = uni.$u.$parent.call(instance, 'u-form-item')
    const form = uni.$u.$parent.call(instance, 'u-form')
    // inputtextareau-form-itemu-formformvalidate
    // form-itemprosform
    if (formItem && form) {
        form.validateField(formItem.prop, () => { }, event)
    }
}

/**
 * @description 'a.b.c'
 * @param {object} obj 
 * @param {string} key 
 * @returns {*}
 */
function getProperty(obj, key) {
    if (!obj) {
        return
    }
    if (typeof key !== 'string' || key === '') {
        return ''
    } if (key.indexOf('.') !== -1) {
        const keys = key.split('.')
        let firstObj = obj[keys[0]] || {}

        for (let i = 1; i < keys.length; i++) {
            if (firstObj) {
                firstObj = firstObj[keys[i]]
            }
        }
        return firstObj
    }
    return obj[key]
}

/**
 * @description 'a.b.c'
 * @param {object} obj 
 * @param {string} key 
 * @param {string} value 
 */
function setProperty(obj, key, value) {
    if (!obj) {
        return
    }
    // 
    const inFn = function (_obj, keys, v) {
        // key
        if (keys.length === 1) {
            _obj[keys[0]] = v
            return
        }
        // 0~length-1key
        while (keys.length > 1) {
            const k = keys[0]
            if (!_obj[k] || (typeof _obj[k] !== 'object')) {
                _obj[k] = {}
            }
            const key = keys.shift()
            // 
            inFn(_obj[k], keys, v)
        }
    }

    if (typeof key !== 'string' || key === '') {

    } else if (key.indexOf('.') !== -1) { // 
        const keys = key.split('.')
        inFn(obj, keys, value)
    } else {
        obj[key] = value
    }
}

/**
 * @description 
 */
function page() {
    const pages = getCurrentPages()
    return `/${getCurrentPages()[pages.length - 1].route}`
}

