/**
 * @api:     OSS
 * @author:  mandy
 */
import {
	UploadApi
} from "@/api/upload";

export const ossUtils = {
	/**
	 * 
	 * @param len         
	 * @return {string}   
	 */
	getRandomString(len) {
		const date = new Date();
		let month = date.getMonth() + 1;
		if (month >= 1 && month <= 9) {
			month = "0" + month;
		}
		let strDate = date.getDate();
		if (strDate >= 0 && strDate <= 9) {
			strDate = "0" + strDate;
		}
		const currentDate = date.getFullYear() + month + strDate;

		len = len || 32;
		const chars = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678";
		const maxPos = chars.length;
		let pwd = "";
		for (let i = 0; i < len; i++) {
			pwd += chars.charAt(Math.floor(Math.random() * maxPos));
		}
		return currentDate + pwd;
	},
	/**
	 * 
	 * @return {string}   
	 * @param filePath
	 */
	getFileSuffix(filePath) {
		console.log(filePath)
		const pos = filePath.lastIndexOf(".");
		let suffix = "";
		if (pos !== -1) {
			suffix = filePath.substring(pos);
		}
		return suffix;
	},
	/**
	 * 
	 * @param file
	 */
	generateRandomFileName(file) {
		console.log(file)
		let randomName = ossUtils.getRandomString(8);
		randomName += ossUtils.getFileSuffix(file);
		return randomName;
	},

	/**
	 * OSS
	 * 1. 
	 * 2. 
	 * @return {Promise<unknown>} promisethen
	 */
	getSignaturePromise() {
		return new Promise((resolve, reject) => {
			UploadApi.getOssInfo().then(res => {
				resolve(res.data);
			}).catch(error => {
				console.log(error)
				error.msg = "OSS";
				reject(error);
			});
		});
	},
	/**
	 * OSS
	 * @param ossSignature    OSS
	 * @param file            
	 * @return {FormData}     
	 */
	getFormData(ossSignature, ossFileName) {
		const ossData = new FormData();
		// policyOSSAccessKeyIdsignature OSS
		ossData.append("policy", ossSignature.policy);
		ossData.append("OSSAccessKeyId", ossSignature.accessid);
		ossData.append("Signature", ossSignature.signature);
		// 200204
		ossData.append("success_action_status", "200");
		// callback OSS
		// ossData.append('callback', ossSignature.callback)
		// key OSS
		ossData.append("key", ossSignature.dir + ossFileName);
		ossData.append("dir", ossSignature.dir);
		// file OSS403
		ossData.append("file", file);
		return ossData;
	},
	/**
	 * OSS
	 * @param file              
	 * @return {Promise<void>}  axiosOSSpromise
	 */
	async uploadFile(file) {
		return new Promise((resolve, reject) => {
			this.getSignaturePromise().then(ossSignature => {



					

					const ossFileName = this.generateRandomFileName(file.tempFilePaths[0]);

				const ossURL = ossSignature.host;
				uni.uploadFile({
					url: ossURL,
					filePath: file.tempFilePaths[0],
					fileType: 'image',
					name: 'file',
					formData: {
						policy: ossSignature.policy,
						OSSAccessKeyId: ossSignature.accessid,
						Signature: ossSignature.signature,
						success_action_status: '200',
						key: ossSignature.dir + ossFileName,
						name: ossSignature.dir + ossFileName
					},
					success: (uploadFileRes) => {
						resolve(ossSignature.host + '/' + ossSignature.dir + ossFileName)
					},
					fail:(err)=>{
						console.log(err, 'oss')
						reject(err)
					}
				});
			}).catch(err => {
				console.log(err)
				reject(err)
			});
		});
	},
};
