export default {
    computed: {
        // 
        value() {
            const {
                text,
                mode,
                format,
                href
            } = this
            // 
            if (mode === 'price') {
                // text
                if (!/^\d+(\.\d+)?$/.test(text)) {
                    uni.$u.error('text');
                }
                // formatformat
                if (uni.$u.test.func(format)) {
                    // 
                    return format(text)
                }
                // format
                return uni.$u.priceFormat(text, 2)
            } if (mode === 'date') {
                // 
                !uni.$u.test.date(text) && uni.$u.error('text')
                // formatformat
                if (uni.$u.test.func(format)) {
                    // 
                    return format(text)
                } if (this.formart) {
                    // format
                    return uni.$u.timeFormat(text, format)
                }
                // format
                return uni.$u.timeFormat(text, 'yyyy-mm-dd')
            } if (mode === 'phone') {
                // 
                !uni.$u.test.mobile(text) && uni.$u.error('text')
                if (uni.$u.test.func(format)) {
                    // 
                    return format(text)
                } if (format === 'encrypt') {
                    // formatencrypt
                    return `${text.substr(0, 3)}****${text.substr(7)}`
                }
                return text
            } if (mode === 'name') {
                // 
                !(typeof (text) === 'string') && uni.$u.error('text')
                if (uni.$u.test.func(format)) {
                    // 
                    return format(text)
                } if (format === 'encrypt') {
                    // formatencrypt
                    return this.formatName(text)
                }
                return text
            } if (mode === 'link') {
                // 
                !uni.$u.test.url(href) && uni.$u.error('hrefURL')
                return text
            }
            return text
        }
    },
    methods: {
        // 
        formatName(name) {
            let value = ''
            if (name.length === 2) {
                value = name.substr(0, 1) + '*'
            } else if (name.length > 2) {
                let char = ''
                for (let i = 0, len = name.length - 2; i < len; i++) {
                    char += '*'
                }
                value = name.substr(0, 1) + char + name.substr(-1, 1)
            } else {
                value = name
            }
            return value
        }
    }
}
