module.exports = {
    // 
    props: {
        // 
        customStyle: {
            type: [Object, String],
            default: () => ({})
        },
        customClass: {
            type: String,
            default: ''
        },
        // 
        url: {
            type: String,
            default: ''
        },
        // 
        linkType: {
            type: String,
            default: 'navigateTo'
        }
    },
    data() {
        return {}
    },
    onLoad() {
        // getRect$uin(this)
        this.$u.getRect = this.$uGetRect
    },
    created() {
        // createdcreated$u
        this.$u.getRect = this.$uGetRect
	},
    computed: {
        // 2.x$uuniuni.$u.xxx
        // computedthis.$ujsuni.$u.xxx
		// nvue$unvueprops
		$u() {

			// nvuepropshttpmixinsetData
			return uni.$u.deepMerge(uni.$u, {
				props: undefined,
				http: undefined,
				mixin: undefined
			})




		},
        /**
		 * bem
		 * H5nvueclass:class="[bem()]"
		 * ['a', 'b', 'c']
		 * @param {String} name 
		 * @param {Array} fixed 
		 * @param {Array} change truefalse
		 * @return Array
		 */
        bem() {
            return function (name, fixed, change) {
                // 
                const prefix = `u-${name}--`
                const classes = {}
                if (fixed) {
                    fixed.map((item) => {
                        // 
                        classes[prefix + this[item]] = true
                    })
                }
                if (change) {
                    change.map((item) => {
                        // this[item]truefalse
                        this[item] ? (classes[prefix + item] = this[item]) : (delete classes[prefix + item])
                    })
                }
                return Object.keys(classes)
            }
        }
    },
    methods: {
        // 
        openPage(urlKey = 'url') {
            const url = this[urlKey]
            if (url) {
                // uni.navigateTo
                uni[this.linkType]({
                    url
                })
            }
        },
        // 
        // bug(2020-07-21)
        // view
        $uGetRect(selector, all) {
            return new Promise((resolve) => {
                uni.createSelectorQuery()
                    .in(this)[all ? 'selectAll' : 'select'](selector)
                    .boundingClientRect((rect) => {
                        if (all && Array.isArray(rect) && rect.length) {
                            resolve(rect)
                        }
                        if (!all && rect) {
                            resolve(rect)
                        }
                    })
                    .exec()
            })
        },
        getParentData(parentName = '') {
            // createdparent
            if (!this.parent) this.parent = {}
            // (u-radiou-radio-groupthis)
            // this(u-radiothis)parentData
            // this.parent.xxx
            // u-radio-groupdata
            this.parent = uni.$u.$parent.call(this, parentName)
            if (this.parent.children) {
                // childrenchildren
                this.parent.children.indexOf(this) === -1 && this.parent.children.push(this)
            }
            if (this.parent && this.parentData) {
                // parentDataparentparentData
                Object.keys(this.parentData).map((key) => {
                    this.parentData[key] = this.parent[key]
                })
            }
        },
        // 
        preventEvent(e) {
            e && typeof (e.stopPropagation) === 'function' && e.stopPropagation()
        },
        // 
        noop(e) {
            this.preventEvent(e)
        }
    },
    onReachBottom() {
        uni.$emit('uOnReachBottom')
    },
    beforeDestroy() {
        // parentchldrencheckboxcheckbox-group
        // children
        if (this.parent && uni.$u.test.array(this.parent.children)) {
            // children
            const childrenList = this.parent.children
            childrenList.map((child, index) => {
                // 
                if (child === this) {
                    childrenList.splice(index, 1)
                }
            })
        }
    }
}
