import {
	RouterMount,
	createRouter,
	runtimeQuit
} from 'uni-simple-router';
const router = createRouter({
	platform: process.env.VUE_APP_PLATFORM,
	routes: [...ROUTES],
	routerErrorEach: ({
		type,
		level,
		...args
	}) => {
		console.log({
			type,
			level,
			...args
		})






	},
});
const whiteList = ['login'];
//
router.beforeEach((to, from, next) => {
	const isLogin = uni.getStorageSync('token') ;
	if (isLogin) {
		next()
	} else {
		if (to.meta.loginAuth) {
			next({
				name: 'login',
				NAVTYPE: 'push'
			})
		} else {
			next()
		}
	}
});

export {
	router,
	RouterMount
}
